.loginpage {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginwrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #6b778b;
}
.loginwrapper form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  padding: 5px;
}
.loginwrapper form label {
  width: 60%;
}
.loginwrapper form label div {
  display: flex;
}

.loginbtn {
  background-color: rgb(33, 28, 37);
  color: #edf5e1;
}

.submitbtn {
  background-color: rgb(46, 49, 90);
  color: #edf5e1;
}/*# sourceMappingURL=login.module.css.map */